<template>
  <div class="convertRecord">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>领取记录</span>
    </header>
    <div class="record_list" v-for="item in list">
      <section class="record_list_c">
        <p><span>礼包类型:</span> <span>{{item.libao_type | typeFilter}}</span></p>
        <p><span>商品名称:</span> <span>{{item.good_name}}</span></p>
        <p><span>领取时间:</span> <span>{{item.create_time | time}}</span></p>
        <p><span>快递名称:</span>  <span>{{item.kd_name}}</span></p>
        <p><span>快递单号:</span> <span>{{item.kd_number}}</span></p>
        <p><span>姓名:</span>  <span>{{item.name}}</span></p>
        <p><span>电话:</span> <span>{{item.tel}}</span></p>
      </section>
      <section class="record_list_r">
        <p>{{item.type | sendFilter}}</p>
      </section>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        list:[]
      }
    },
    filters:{
      typeFilter(val){
        if (val === 1){
          return "开店礼包"
        } else {
          return "直推礼包"
        }
      },
      sendFilter(val){
        if (val === 0){
          return "未发货"
        } else {
          return "已发货"
        }
      }
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      record(page=1){
        this.post('home/Order/getLiBaoOrder',{
          page: page,
          number:20
        }).then(res => {
          if (res.data.code === 10000){
            this.list = res.data.info.info;
          }
        })
      }
    },
    mounted(){
      this.record();
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  .convertRecord {
    height: 100vh;
    background-color: #f2f2f2;
  }
  .record_list {
    padding: 0.3rem 0.2rem;
    background-color: #ffffff;
    margin-top: 0.2rem;
    display: flex;
  }
  .record_list_c{
    flex: 1;
  }
  .record_list_c span:first-child{
    display: inline-block;
    width: 1.2rem;
    color: #444444;
    font-size: 0.28rem;
    margin-bottom: 0.1rem;
  }
  .record_list_c span:last-child {
    color: #111111;
    font-size: 0.3rem;
  }
  .record_list_r p{
    color: #ff6f00;
    font-size: 0.28rem
  ;
  }
</style>



